				userAuthData['sex'] = this.userInfo.gender == 1 ? '男' : '女'
				userAuthData['school'] = this.userInfo.userAuthData['school']['result'][1]
			}

			return [{
				icon: 'friendfill',
				text: '学校：' + userAuthData['school']
